    color:#7E3ABA;
    font-weight: bolder;
    font-size: 1.5vw;
    cursor: pointer;
    text-decoration: none;
}


.logo_header{
    width: 60px;
    height:60px;
    cursor: pointer;
    border-radius: 150px;
    border: 2px solid white;
}
.logo_footer{
    width: 60px;
    height:60px;
    cursor: pointer;
    border-radius: 150px;
    border: 2px solid white;
}
.Mailform__container{
    margin-top: 50px;
    margin-bottom: 100px;
    height: 400px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.form__container{
    background-color: #e1cef1;
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    border: 4px solid #7E3ABA;
    border-radius: 30px;
}

.form_title{
    color: #7E3ABA;
    font-size: 30px;
}

.form_input{
    margin: 10px;
    border-radius: 30px;
    height:40px;
    width: 40%;
}
.form_textarea{
    margin: 10px;
    border-radius: 10px;
    width: 100%;
    height: 35%;
}
::-webkit-input-placeholder{
    text-align: center;
    font-size: 15px;
    font-style: oblique;
    color: #7E3ABA;
}

.button_submit{
    background-color: #7E3ABA;
    width: 80px;
    height: 40px;
    margin: 20px;
    border-radius: 20px;
    border: 2px solid black;
    cursor: pointer;
    color: white;
}
.header__container{
    width: 100%;
    height: 400px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: -webkit-linear-gradient(90deg,#af80da 10%, #7E3ABA  70%);
}
.header_logo_container{
    margin-left:10px;
    margin-top: 10px;
}
.profile_section{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.profilePicture{
    width: 35%;
    border-radius: 80px;
    margin-top: 50px;
    cursor:pointer;
    border: 5px solid #7E3ABA;

}
.headerIcon_container{
    margin-right: 10px;
    margin-top: 10px;
}
.headerIcon{
    width: 30px;
    margin:10px;
    cursor:pointer;
}
.name_title{
    font-weight: bolder;
    font-size: 3vw;
    font-family: 'Bonheur Royale', cursive;
cursor: pointer;
text-shadow: 1px 1px 2px black;

    color: white;
}
.position_title{
    font-weight: bolder;
    font-size: 2vw;
    font-family: 'Architects Daughter', cursive;
    color: white;
    text-transform: uppercase;
    text-shadow: 1px 1px 2px black;
}
.skills__container{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 100px;

}
.skills_header{
    width: 150px;
    height: 150px;
    border-radius: 150px;
    background: -webkit-linear-gradient(90deg, #7E3ABA  10%, #af80da 50%);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    -webkit-animation: skillsHeader 20s linear 2s infinite alternate backwards running;
            animation: skillsHeader 20s linear 2s infinite alternate backwards running;
    color:white;
    font-weight: bolder;
    font-size: 1.7vw;
    cursor: pointer;

}

@-webkit-keyframes skillsHeader {
    0%{
    right:0px;
    top:0px;
    }
    25%{
    top:300px;
    right:0px
    }
    50%{
        top:0px;
        right:0px
    }
    75%{
        top:300px;
        right:0px;
    }
    100%{
        right:0px;
        top:0px;
    }
  }

@keyframes skillsHeader {
    0%{
    right:0px;
    top:0px;
    }
    25%{
    top:300px;
    right:0px
    }
    50%{
        top:0px;
        right:0px
    }
    75%{
        top:300px;
        right:0px;
    }
    100%{
        right:0px;
        top:0px;
    }
  }

  .skills_button_container{
        width:30%;
        background-color: #e1cef1;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center; 
        margin-right: 100px;
        border-radius: 20px;
        position: relative;
        -webkit-animation: skillsItem 8s ease 2s infinite alternate backwards running;
                animation: skillsItem 8s ease 2s infinite alternate backwards running;
}
.first_list_skills, .second_list_skills, .third_list_skills{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    position: relative;
    -webkit-animation: skillsItem 8s ease 2s infinite alternate backwards running;
            animation: skillsItem 8s ease 2s infinite alternate backwards running;
    }
.list_skill{
    margin: 10px;
    color:  #7E3ABA;
    list-style-type: none;
    font-weight: bolder;
    font-size: large;
    font-family: 'Times New Roman', Times, serif;
    font-style: oblique;
    cursor: pointer;
    position: relative;
    -webkit-animation: skillsItem 8s ease 2s infinite alternate backwards running;
            animation: skillsItem 8s ease 2s infinite alternate backwards running;
}
@-webkit-keyframes skillsItem {
    0%{
        left:0px;
        top:0px
    }
    25%{
        left:100px
        }
        50%{
            left:150px
        }
        75%{
            left:250px;
        }
        100%{
            left:0px;
        }
  }
@keyframes skillsItem {
    0%{
        left:0px;
        top:0px
    }
    25%{
        left:100px
        }
        50%{
            left:150px
        }
        75%{
            left:250px;
        }
        100%{
            left:0px;
        }
  }
.projects_section_container{
